import React,{ useEffect, useState } from 'react';
import './app.css';
import SearchBar from './components/searchBar.jsx';
import Main from './components/main';
import Detail from './components/detail';
import {Route} from 'react-router-dom'
import axios from 'axios';
function App() {
 
  let[lists, setList] = useState([]);
  let[detail, setDetail] = useState({});

    useEffect(() => {
        let MostPopularUrl = 'https://youtube.googleapis.com/youtube/v3/videos?part=snippet&chart=mostPopular&maxResults=25&key=AIzaSyCBEqHMrpU-L7VQKFCohacpTaNnZNe8YV8'; 
        axios.get(MostPopularUrl)
        .then((response)=>{
            makeitem(response.data.items);
        })
        .catch((error)=>{
          console.log(error);
        })
        
        const makeitem = (item)=>{
           const list = item.map(element => {
            const url = element.snippet.thumbnails.medium.url;  
            const channelTitle = element.snippet.channelTitle;
            const title = element.snippet.title;
            const id = element.id
            const description = element.snippet.description;
            return {
                id,
                url,
                title,
                channelTitle,
                description
                }
            
            
            });
          
            setList(list);
           
        }
        
       
        
        
      }, [])
      
      const searchList =  (item) =>{
       
        setList([]);
        setList(item);
        
      }
      const detailData = (id, title, ctitle, description) =>{
       setDetail({
         id,
         title,
         ctitle,
         description
       })
       
      }


  return (
    <>

    <SearchBar searchList={searchList} />

    <Route exact path="/">
    <Main lists={lists} detailData={detailData}></Main>
    </Route>

    <Route path="/detail">
    <Detail lists={lists} detail={detail} detailData={detailData} />
    </Route>
 

    </>
  );
}

export default App;
