import React, { useRef, useState } from 'react';
import style from '../componentscss/searchBar.module.css';
import '@fortawesome/fontawesome-free/js/all.js'
import axios from 'axios'
import {Link, useHistory} from 'react-router-dom';
const SearchBar = (props) => {
    
        let q = useRef(null);
        let formRef = useRef(null);
        let history = useHistory();

    const onsubmit = (e) =>{
        e.preventDefault();
        let url = `https://youtube.googleapis.com/youtube/v3/search?part=snippet&maxResults=25&q=${q.current.value}&key=AIzaSyCBEqHMrpU-L7VQKFCohacpTaNnZNe8YV8`;
        
        axios.get(url)
        .then(function (response) {
          makeSearch(response.data.items);
        })
        .catch(function (error) {
          console.log(error);
        });
        const makeSearch = (item)=>{
          const searchList = item.map((item)=>{
            const url = item.snippet.thumbnails.medium.url;  
            const channelTitle = item.snippet.channelTitle;
            const title = item.snippet.title;
            const id = item.id.videoId;
            const description = item.snippet.description;
            return {
              id,
              url,
              title,
              channelTitle,
              description
            }
            
          })
          
          props.searchList(searchList);
        }
          history.push("/")
         formRef.current.reset();

    }

    return(
        
        <div className={style.container}>

            <Link to="/">
            <div className={style.logo}>
            <i className="fab fa-youtube"></i>
            </div>
            </Link>
            <Link to="/">
            <span className={style.text}>WonTube</span>
            </Link>
            <form ref={formRef} onSubmit={onsubmit}>
            <input className={style.search}
            ref={q}
             type='text'
             placeholder='Search...'
             ></input>
            <button className={style.button}>
            <i class="fas fa-search"></i>
            </button>
            </form>
        </div>
        
    );
}
            
    

export default SearchBar;