import React from 'react';
import style from '../componentscss/detail.module.css'
const Detail = (props) => {

    let imageSrc = `https://www.youtube.com/embed/${props.detail.id}`

    
    return(
            <div className={style.container}>
                
                <div className={style.content}>
               <iframe className={style.youtube}  
               id="ytplayer" type="text/html" 
               width="650" height="300"
                src={imageSrc}
                frameborder="0" allowfullscreen></iframe> 
                <h2>{props.detail.title}</h2>
                <h4>{props.detail.ctitle}</h4>
                <p>{props.detail.description}</p>
                 </div>

                <div className={style.leftContainer}>
                
                {
                    props.lists.map((item)=>{
                        return(
                <div className={style.item} key={item.id}
                 onClick={()=>{
                    props.detailData(item.id, item.title, item.channelTitle, item.description);
                 }}
                >
                <img className={style.itemimage} src={item.url}></img>
                <ul className={style.itemtext}>
                    <li>{item.title}</li>
                    <li className={style.write}>{item.channelTitle}</li>
                </ul>
                 </div>
                        )
                    })
                }

                </div>

            </div>
    )
    };
export default Detail;